.side-menu {
  width: 20%;
  background: #2980b9;
  background: -webkit-gradient(linear, left top, left bottom, from(#2c3e50), to(#2980b9));
  background: linear-gradient(to bottom, #2c3e50, #2980b9);

  color: white;
  min-height: 100vh;
  overflow: hidden;
  box-shadow: 6px 0px 8px -2px rgba(0, 0, 0, 0.4);
}
.side-menu h1 {
  padding-top: 20px;
  padding-bottom: 40px;
  text-align: center;
  cursor: pointer;
}

.side-menu .menu-item {
  padding: 20px;
  cursor: pointer;
  border-left: 3px solid transparent;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.side-menu span {
  margin-left: 20px;
  font-size: 18px;
}
.side-menu .menu-item:hover {
  background-color: white;
  color: #2980b9;
  border-left: 3px solid;
  border-color: linear-gradient(to top, #2c3e50, #2980b9);
}
.side-menu .menu-item:hover .menu-icons {
  background: radial-gradient(
    circle at 33% 100%,
    #2c3e50 50%,
    #2980b9 80%,
    #2c3e50
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.side-menu .menu-item i {
  font-size: 25px;
}

:root {
  --main-color: #2c3e50;
  --secondary-color: #2980b9;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
  background-color: #eee;
  display: flex;
}

.content {
  width: 100%;
  margin: 0 20px;
}
.frames {
  display: flex;
}

.frame {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
}
.frame .head {
  display: relative;
}
.frame .head h3 {
  margin: 0;
  color: #888;
}
.frame .head p {
  color: #bbb;
  font-size: 12px;
  white-space: nowrap;
}
.frame h4 {
  color: #2980b9;
  color: var(--secondary-color);
  position: relative;
  top: -65px;
  right: 0px;
  float: right;
}
.announcements {
  margin-right: 20px;
}

nav {
  padding: 15px;
  margin-left: 5px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav h1 {
  color: #888;
  margin-left: 20px;
}
nav .right {
  display: flex;
}
nav input {
  border: 1px solid #ddd;
  border-top-left-radius: 27px;
  border-bottom-left-radius: 27px;
  border-top-right-radius: 27px;
  border-bottom-right-radius: 27px;
  padding: 0.5rem 0.5rem 0.5rem 2.5rem;
  width: 250px;
  outline: none;
}
nav .right div {
  position: relative;
}
nav .right div i {
  position: absolute;
  top: 12px;
  left: 12px;
  color: #ddd;
  cursor: pointer;
}
nav .right > i {
  position: relative;

  margin-left: 30px;
  cursor: pointer;
  background: radial-gradient(
    circle at 33% 100%,
    #2c3e50 50%,
    #2980b9 80%,
    #2c3e50
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
nav .right .img {
  background: url(https://www.w3schools.com/howto/img_avatar2.png);
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 30px;
  overflow: hidden;
  border-radius: 50px;
  width: 40px;
  height: 40px;
}
nav .right > span {
  content: "100";
  position: relative;
  background: #2980b9;
  height: 20px;
  top: 0;
  right: 7px;
  width: 20px;
  text-align: center;
  line-height: 20px;
  font-size: 1rem;
  border-radius: 50%;
  color: white;
  border: 1px solid #fff;
}

.exams {
  display: flex;
  background-color: white;
  margin: 20px 0;
  overflow: hidden;
}
.exams .content {
  width: 50%;
  padding: 20px;
  padding-left: 30px;
}
.exams .content h1 {
  font-size: 45px;
  background: -webkit-gradient(linear, left top, right top, from(#2980b9), to(#2c3e50));
  background: linear-gradient(to right, #2980b9 0%, #2c3e50 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.exams p {
  color: #888;
}
.exams .quote {
  color: #aaa;
}
.exams .image {
  background: url(/static/media/exams.2bb11abb.svg);
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 30px;
  overflow: hidden;
  width: 50%;
}

.exams button {
  padding: 10px 35px;
  background-color: #2c3e50;
  color: #fff;
  border: none;
  outline: none;
  border-radius: 10px;
  font-weight: bold;
  margin-top: 15px;
}

.right,
.left {
  padding: 0 10px;
}
.comment {
  display: flex;
}
.comment .left {
  display: flex;
  border-right: 1px solid #ddd;
  width: 320px;
}
.comment .right {
}
.comment .left h5 {
  font-size: 16px;
  margin: 0;
  color: #888;
}
.comment .left p {
  color: #aaa;
  margin: 0;
}
.comment {
  display: flex;
  margin-top: 15px;
}
.comment .left img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 5px;
}
.comment .right p {
  color: #aaa;
  margin: 0;
  font-size: 14px;
}

.quiz-body {
  border-bottom: 1px solid #ddd;
  margin: 15px 0;
}
.quiz-body .quiz-head i {
  color: #2980b9;
  margin-left: 5px;
}
.quiz-body .quiz-head span {
  color: #888;
  margin-left: 5px;
  margin-bottom: 5px;
  font-weight: 500;
}
.quiz-body .data {
  display: grid;
  grid-template-columns: 60px auto;
  grid-gap: 10px;
}
.quiz-body .data p {
  margin: 0;
  color: #aaa;
  font-size: 14px;
}

.btn {
  display: block;
  width: 100%;
  color: #2980b9;
  border: 1px solid #2980b9;
  padding: 5px;
  background-color: #fff;
  outline: none;
  margin: 10px 0;
  border-radius: 10px;
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
}
.btn:hover {
  color: #fff;
  background-color: #2980b9;
}

